'use strict';

import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import banesCtrl from './banes.controller';
import './banes.tpl';
import './banes.header.tpl';
import './banes.css!';

const banesModule = angular.module('banes', [
  mainwrap.name,
  'app/banes/banes.tpl.html',
  'app/banes/banes.header.tpl.html',
  banesCtrl.name
]);

banesModule.config(function($stateProvider){
  $stateProvider.state('banes', {
    url: '/banes',
    templateUrl: 'app/banes/banes.tpl.html',
    controller: 'BanesCtrl',
    controllerAs: 'banesCtrl',
    authenticate: false
  });
});

export default banesModule;
